::-webkit-scrollbar {
    width: 13px;
  }
  
  /* Track 남은 부분*/
  ::-webkit-scrollbar-track {
    background:rgba(209, 198, 175, 0.445);; 
  }
   
  /* Handle 스크롤바 */
  ::-webkit-scrollbar-thumb {
    background: rgb(209, 193, 158); 
    border-radius: 30px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background:rgb(95, 178, 255);; 
  }
section.first-page{
    z-index: -10;
    height: 100vh;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* scroll-snap-align: center; */
}
 
.first-page .left-side-page{
    position: relative;
    z-index: -6;
    width: 50vw;
    height: 100vh;
    background-color: rgb(226, 222, 219);
    overflow: hidden;
}

.first-page .first-page-title {
    z-index: 1;
    position: absolute;
    top: 10%;
    left: 60px;
    width: 60vw;
    font-family: 'Chosunilbo_myungjo', serif;
    font-size: 60px;
    font-weight: lighter;
    line-height: 100px;
