/* Modal in LetterDetail */

.black-bg{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: 10;
    background-color: #d3cfc144;
}
.button-cart{
    position: relative;
    width: 500px;
    height: 500px;
    top: -4%;
    left: -2.3%;
    z-index: 20;
    font-size: 45px;
    font-weight: bolder;
    text-shadow: 4px 4px #c9c0aa;
    letter-spacing: 4px;
    color:#dcd5c6;
    padding: 16.5px 0px 0px 120px;
    background-color: #dcd5c6;
    transition: all 0.7s;
    animation: opacity 2.5s;
    cursor: pointer;
}
.button-cart:hover{
    top: -11.3%;
    left: -6%;
    color: white;
}
.button-cart-text{
    position: absolute;
    top: 20%;
    left: 7.5%;
    width: 10px;
    font-size: 45px;
    color: #ebe2cdd0;
    text-shadow: 3px 3px #7f7359;
    line-height: 80px;
}
.modal-x{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 21;
    font-size: 40px;
    color: #a9a499c2;
    position: absolute;
    left: 102%;
    top: -1px;
    font-weight: bolder;
}
.modal-close{
    z-index: 21;
    position: absolute;
    left: 100%;
    top: 17px;
    width: 60px;
    cursor: pointer;
    font-size: 40px;
    font-weight: bolder;
    text-align: center;
    line-height: 75px;
    color: #87837be7;
    cursor: pointer;
    transition: ease 0.5s;
}
.modal-close:hover{
    background-color: rgb(255, 255, 255);
}
.modal-wrap{
    width: 500px;
    height: 500px;
    position: absolute;
    background:rgb(223, 223, 223);
    z-index: 20;
    animation: opacity 1s;
}
.detail-box{
    position: absolute;
    z-index:21;
    width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center; 
}
